export default "\nimport * as React from 'react';\nimport * as ReactDOM from 'react-dom';\nimport * as uuid from 'uuid';\nimport * as invariant from 'invariant';\nimport { Component, Input, OnInit, OnDestroy, OnChanges, AfterViewInit } from '@angular/core';\nimport { Kpi } from '@gooddata/react-components';\nimport {\n  projectId,\n  totalSalesIdentifier\n} from '../../../utils/fixtures.js';\n\ninterface KpiProps {\n  measure: string;\n  projectId: any;\n  format?: string;\n  filters?: any[];\n  onLoadingChanged?: (any);\n  onError?: (any);\n}\n\n@Component({\n  selector: 'app-kpi',\n  template: '<span [id]=\"rootDomID\"></span>'\n})\n\nexport class KpiComponent implements OnInit, OnDestroy, OnChanges, AfterViewInit {\n  @Input() filters: any[];\n  @Input() format: string;\n  @Input() onLoadingChanged?: (any);\n  @Input() onError?: (any);\n\n  public rootDomID: string;\n\n  protected getRootDomNode() {\n    const node = document.getElementById(this.rootDomID);\n    invariant(node, `Node '${this.rootDomID} not found!`);\n    return node;\n  }\n\n  protected getProps(): KpiProps {\n    return {\n      projectId: projectId,\n      measure: totalSalesIdentifier,\n      format: this.format,\n      filters: this.filters,\n      onLoadingChanged: this.onLoadingChanged,\n      onError: this.onError\n    };\n  }\n\n  private isMounted(): boolean {\n    return !!this.rootDomID;\n  }\n\n  protected render() {\n    if (this.isMounted()) {\n      ReactDOM.render(React.createElement(Kpi, this.getProps()), this.getRootDomNode());\n    }\n  }\n\n  ngOnInit() {\n    this.rootDomID = uuid.v1();\n  }\n\n  ngOnChanges() {\n    this.render();\n  }\n\n  ngAfterViewInit() {\n    this.render();\n  }\n\n  ngOnDestroy() {\n    // Uncomment if Angular 4 issue that ngOnDestroy is called AFTER DOM node removal is resolved\n    // ReactDOM.unmountComponentAtNode(this.getRootDomNode())\n  }\n}\n"